/*-----------------------------------gallery------------------------------------------------------------*/

.gallery1
{
    margin-top: 80px;
}
.gallery {
    -webkit-column-count: 1;
    -moz-column-count: 1;
    column-count: 3;
    -webkit-column-width: 33%;
    -moz-column-width: 33%;
    column-width: 33%;
}
.overlay {
    position: absolute;
    bottom: 100%;
    left: 0;
    right: 0;
    background-color:#5D42D3;
    overflow: hidden;
    width: 100%;
    height:0;
    transition: 1s ease;
  }
  .text {
    color: #909090;
    font-size: 1.4vw;
    position: absolute;
    font-family: sans-serif;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    text-align: center;
  }
  .gallery:hover .overlay {
    bottom: 0;
    height: 100%;
  }
    .gallery .pics {
    -webkit-transition: all 350ms ease;
    transition: all 350ms ease; }
    .gallery .animation {
    -webkit-transform: scale(1);
    -ms-transform: scale(1);
    transform: scale(1); }
    
    @media (max-width: 450px) {
    .gallery {
    -webkit-column-count: 1;
    -moz-column-count: 1;
    column-count: 1;
    -webkit-column-width: 100%;
    -moz-column-width: 100%;
    column-width: 100%;
    }
    }
    
    @media (max-width: 400px) {
    .btn.filter {
    padding-left: 1.1rem;
    padding-right: 1.1rem;
    }
    }
    .gallery__bgColor
    {
        background-color: #4a4a4a;
    }
